body {
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

/* for 01-bubbling */
#parent,
#child,
#inner-child {
  margin: 16px;
  padding: 16px;
  border: 1px solid #ffc107;
  border-radius: 4px;
  color: #fff;
  font-weight: 500;
  font-size: 18px;
}

#parent {
  background-color: #673ab7;
}

#child {
  background-color: #448aff;
}

#inner-child {
  background-color: #009688;
}

/* for 02-delegation */
.container {
  display: flex;
  flex-wrap: wrap;
  outline: 2px solid tomato;
}

/* for 03-tags */

.tags {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 8px;
  background-color: #fff;
  list-style: none;
  user-select: none;
  outline: 1px solid tomato;
}

.tags__item {
  margin: 4px;
}

.tags__btn {
  border: 1px solid #212121;
  border-radius: 4px;
  display: inline-block;
  padding: 4px 10px 4px;

  font-size: 24px;
  font-family: inherit;
  color: #212121;
  font-weight: 500;
  text-align: center;
  white-space: nowrap;
  text-decoration: none;
  outline: none;

  background-color: #fff;
  cursor: pointer;
}

.tags__btn--active {
  color: #fff;
  background-color: #3f51b5;
}

/* for 04-colorpicker */
.palette {
  display: grid;
  max-width: 1200px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 12px;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}

.color-card {
  padding: 5px;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.color-card.is-active {
  transform: perspective(400px) rotateX(10deg);
}

.color-swatch {
  height: 128px;
  cursor: pointer;
}

.color-meta {
  padding: 10px;
  font-weight: 700;
}

.color-meta p {
  margin-top: 0;
  margin-bottom: 0;
  letter-spacing: 0.1em;
}


