body {
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

#parent, #child, #inner-child {
  color: #fff;
  border: 1px solid #ffc107;
  border-radius: 4px;
  margin: 16px;
  padding: 16px;
  font-size: 18px;
  font-weight: 500;
}

#parent {
  background-color: #673ab7;
}

#child {
  background-color: #448aff;
}

#inner-child {
  background-color: #009688;
}

.container {
  outline: 2px solid tomato;
  flex-wrap: wrap;
  display: flex;
}

.tags {
  -webkit-user-select: none;
  user-select: none;
  background-color: #fff;
  outline: 1px solid tomato;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 8px;
  list-style: none;
  display: flex;
}

.tags__item {
  margin: 4px;
}

.tags__btn {
  color: #212121;
  text-align: center;
  white-space: nowrap;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #212121;
  border-radius: 4px;
  outline: none;
  padding: 4px 10px;
  font-family: inherit;
  font-size: 24px;
  font-weight: 500;
  text-decoration: none;
  display: inline-block;
}

.tags__btn--active {
  color: #fff;
  background-color: #3f51b5;
}

.palette {
  max-width: 1200px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.color-card {
  background-color: #fff;
  border-radius: 4px;
  padding: 5px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 2px 1px -1px #0003, 0 1px 1px #00000024, 0 1px 3px #0000001f;
}

.color-card.is-active {
  transform: perspective(400px)rotateX(10deg);
}

.color-swatch {
  height: 128px;
  cursor: pointer;
}

.color-meta {
  padding: 10px;
  font-weight: 700;
}

.color-meta p {
  letter-spacing: .1em;
  margin-top: 0;
  margin-bottom: 0;
}

